import React from "react";
import { InputNumber, Select } from 'antd';

const { Option } = Select;

const selectAfter = ({defaultUnit = 'in'}) => (
    <Select defaultValue={defaultUnit} className="select-after">
        <Option value="in">in</Option>
        <Option value="mm">mm</Option>
    </Select>
);

const MeasurementInput = ({ defaultValue = '', defaultUnit = 'in' }) => (
    <InputNumber addonAfter={selectAfter(defaultUnit)} defaultValue={defaultValue} />
);

export default MeasurementInput;
