import React, {useState} from "react";
import {Form, Select, Space} from 'antd';
import MeasurementInput from "./MeasurementInput";

const Truck = ({options, truck, setTruck = f => f}) => {

    const [customTruck, setCustomTruck] = useState({
        axle_width_value: 7.25,
        axle_width_unit: "in",
        hanger_width_value: 4.6,
        hanger_width_unit: "in",
        ...options[0]
    });

    const onchangeSelect = (item) => {
        if (item === 0) {
            setTruck(customTruck)
        }else{
            setTruck(options[item]);
        }
    };

    const customImput = (
        <>
            <Form.Item label="Axle Width">
                <MeasurementInput defaultValue={customTruck.axle_width_value} defaultUnit={customTruck.axle_width_unit}/>
            </Form.Item>
            <Form.Item label="Hanger Width">
                <MeasurementInput defaultValue={customTruck.hanger_width_value} defaultUnit={customTruck.hanger_width_unit}/>
            </Form.Item>
        </>
    )

    return (<div className="Truck">
        <Space direction="vertical">
            <Form.Item label="Truck">
                <Select
                    value={truck.value}
                    options={options}
                    onSelect={onchangeSelect}
                    style={{ width: 250 }}
                />
            </Form.Item>
            {truck.value === 0 && customImput}
        </Space>
    </div>);
};

export default Truck;
